.App {
  text-align:center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  position:sticky;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


footer{
  background-color:white;
  position:fixed;
  bottom:0px;
  text-align:right;
  width:100%;
  box-shadow:0px 0px 10px silver;
  z-index:99999;
}

/* Mobile 320px */
@media screen and (max-width:320px)
{
body{
  width:100%;
}
.App{
  width:100%;
}

main.container{
  position:relative;
  top:65px;
}
main.container h1
{
  font-size:1.5rem;
  text-align:left;
}
}

